import React, { Component, PureComponent } from 'react';
import PropTypes from 'prop-types';
import {
  IconGithub,
  IconLinkedin,
  IconCodeSandbox,
  IconDribbble,
  IconDesktop,
  IconPhone,
  IconTwitter,
  IconBehance,
} from './icons';
import styled, { css } from 'styled-components';

import { media } from '../styles';

const items = [IconPhone, IconDesktop];
const sizes = {
  small: 26,
  medium: 32,
  large: 42,
};

const SvgIcon = styled.svg`
  width: ${sizes.large}px;
  height: ${sizes.large}px;

  ${media.desktop`width: ${sizes.large}px; height: ${sizes.large}px;`};
  ${media.tablet`width: ${sizes.large}px; height: ${sizes.large}px;`};
  ${media.tablet`width: ${sizes.medium}px; height: ${sizes.medium}px;`};
  ${media.phone`width: ${sizes.small}px; height: ${sizes.small}px;`};
`;

const Container = styled.div`
  display: inline-flex;
  ${media.tablet`font-size: 2rem`};
`;

const IconBox = styled.span`
  position: relative;
  display: inline-flex;
  align-items: center;
  margin: 0px 0.3rem 0px 0.3rem;
`;

class Interfaces extends PureComponent {
  random_item(items) {
    return items[Math.floor(Math.random() * items.length)];
  }
  render() {
    const item = this.random_item(items);
    return (
      <Container>
        Design
        <IconBox>
          <SvgIcon>{item()}</SvgIcon>
        </IconBox>
        interfaces.
      </Container>
    );
  }
}

export default Interfaces;
